.Calendar{
    margin-top:10px;
    margin: 0 auto;
    width: 90%;
    min-width: 1182px;
    padding-bottom: 50px;
}
.CalendarControls{
    display: flex;
    align-items: center;
    /* justify-content: center; */
    /* background-color: white; */
    padding: 8px 0 8px 0px;
}
.Calendar .flatPickerClass input{
    padding: 0 0 0;
    margin:0 5px;
    max-width: 120px;
    min-width: 100px;
    background-color: transparent;
}
.CalendarControls .flatPickerClass{
    min-width: 100px;
    max-width: 120px;
}
/* .CalendarControls .flatPickerClass button{
    border: solid 1px black;
} */
 .dateControls{
    width: 60%;
    float: left;
 }    
.CalendarControls h4{
   margin:0;
   width:20%
}
.Calendar .flatPickerDiv label{
    margin:0 8px 
}
/* .CalendarControls .flatPickerDiv{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
} */
.navControl{
    width: 35%;
    float: right;
    display: flex;
    justify-content: flex-end;
}
.navControl>div{
    margin: 0 4px;
}
.navControl .pipe{
    line-height: 1;
}
.CalendarLedger{
    flex-direction: row;
    display: flex;
    min-width: 550px;
    width: 95%;
    justify-content: flex-end;
}
.CalendarLedger .ledgerIconDiv{
    margin:0  8px;
}
.calendar_icon{
    font-size: 0.8em;
}
.calendar_icon_red,.calendar_icon_red::selection{ 
    background-color: red;
    color:red;
}
.calendar_icon_green,.calendar_icon_green::selection{
    background-color: green;
    color:green;
}
.calendar_icon_yellow,.calendar_icon_yellow::selection{
    color:rgb(221, 204, 162);
}
.calendar_icon_white,.calendar_icon_white::selection{
    border: 0.5px solid black;
    color:white;
}
.calendar_icon_grey{
    background-color: #4f4e4d;
    color:#4f4e4d;
}
.starIconClass{
    background-color: transparent;
    border:none;
    font-size: 0.8em;
    font-weight: bold;
}
.CalendarHeadRow{
    padding:1em;
    text-align: center;
}
.CalendarGrid >div {
    color: white;
    background-color: #4f4e4d;
}
.CalendarGrid table {
    border-collapse: collapse;
    /* border-spacing: 0; */
    width: 100%;
    border: 1px solid #ddd;
  }
.CalendarGrid th,.CalendarGrid td {
    text-align: center;
    border: 1px solid black;
    font-weight: inherit!important;
    padding: 2px;    
    min-width: 75px;
}
.CalendarGrid tr td:first-child{
    min-width: 120px;
    word-break: break-all;
}
/* .CalendarGrid td:hover{
    color: white;
    border:1px solid #ddd;
    background-color: #4f4e4d;
}
.CalendarGrid tr td:first-child:hover{
    background-color:inherit;
    border:1px solid #4f4e4d;
    color: inherit;
} */
.CalendarGrid th:last-child{
    border-right:1px solid #4f4e4d;
}
.CalendarGrid th:first-child{
    border-left:1px solid #4f4e4d;
}
.CalendarGrid th{
    border: 1px solid #ddd;
    border-bottom: none;
    padding: 8px 0;
    background-color: #4f4e4d;
    color: white;
}
.CalendarHeading{
    background-color:white;
}
.CalendarHeading h3{
    padding:0 1em;
    background-color:white;
}
.CalendarGrid td{
    padding: 0;
}
.CalendarGrid tr:nth-child(even){
    background-color: #dddddd;
    color:black;
}
.CalendarCoinClass,.CalendarIndications{
    text-align: center;
    margin: 0 auto;
}
.calendatCoinDiv{
    cursor: pointer;
    padding: 0.2em;
    border-bottom: 0;
    border-top: 0;
}
.CalendarGrid{
    width:75%;
}
.ActivityCards{
    margin:0;
    width: 25%;        
    overflow: scroll;        
}
.ActivityCards h4{
    margin: 0;    
}
.CalendarTitles{
    margin:1em auto;
    width: 90%;
}
.CalendarMain .PageTileSection{
    display: flex;
}
.CalendarMain  .PageTile{
    padding: 1em;
    margin: 0.5em;
    cursor: pointer;
    /* background-color: white; */
    display: flex;
    align-items: center;
    justify-content: center;
}
.CalendarMain  .PageTileContent{
    padding: 0.5em;
    text-align: center;
}
.CalendarMain  .transparentBg{
    background-color: transparent!important;
}
.CalendarMain  .PageTile:hover{
    background-color: #4f4e4d;
    color: white;
}
.CalendarBody{
    display: flex;
    /* height: 425px; */
    flex-direction: row;
    justify-content: space-evenly;
}
.activityContaitner{
    padding: 0 10px;
    text-align: justify;
}
.activityContaitner a{
    color:white;
    font-size:18px;
    text-decoration: none;
}
.activityContaitner a:hover{
    text-decoration: underline;
}
.activityHeadingSection h2{
    margin: 7px 0;
    margin-bottom:5px;
    text-align: center;
}
.activityContaitner ul{
    list-style-type:none;
    margin:0;
}
.Calendar .flatPickerClass{
    min-width: 130px;
    max-width: 125px;
}
.Calendar .flatPickerClass input{
    max-width: 125px;
    min-width: 120px;
}
.Calendar .flatPickerDiv{
    float: right!important;
    display: flex;
    align-items: center;
    min-width: 225px;
    margin: -4px -4px;
    justify-content: flex-end;
}
.CalendarCoinClass div:first-child {
    border-left: 0;
}
.CalendarCoinClass div:last-child {
    border-right: 0;
}
.Calendar tbody tr:last-child {
    background-color: #4f4e4d!important;
    color: white!important;
    font-size: 14px;
}
@media  screen and (max-width: 1440px) {
    .CalControl{
        width: 45%!important;
    }
    .CalMainControl{
        width: 70%!important;
    }
    .navControl{
        width: 50%!important;
    }
    .dateControls{
        width: 42%!important;
    }
    .CalendarLedger{
        width: 55%!important;
    }
}
@media screen and (min-width: 1025px) and (max-width: 1350px) {
  
    .CalControl{
        width: 46%!important;
    }
    .CalMainControl{
        width: 74%!important;
    }
    .navControl{
        width: 50%!important;
    }
    .dateControls{
        width: 40%!important;
    }
    .CalendarLedger{
        width: 54%!important;
        font-size: 15px;
    }
    .CalendarLedger .ledgerIconDiv {
        margin: 0 5px;
    }  
    .Calendar .flatPickerClass {
        min-width: 110px;
        max-width: 110px;
    } 
    .Calendar .flatPickerClass input {
        max-width: 100px;
        min-width: 100px;
    } 
  }
@media screen and  (min-width: 1441px) and (max-width: 1919px) {
    .CalControl{
        width: 46%!important;
    }
    .CalMainControl{
        width: 75%!important;
    }
    .navControl{
        width: 44%!important;
    }
    .dateControls{
        width: 52%!important;
    }
    .CalendarLedger{
        width: 54%!important;
    }
}
@media screen and  (min-width: 1920px) and (max-width: 260px) {
    .CalControl{
        width: 46%!important;
    }
    .CalMainControl{
        width: 75%!important;
    }
    /* .navControl{
        width: 44%!important;
    }
    .dateControls{
        width: 50%!important;
    } */
    .CalendarLedger{
        width: 54%!important;
    }
}